<template>
  <div class="outer">
    <p class="top"></p>

    <p class="group"> </p> <br>
    <table class="info">
      <colgroup>
        <col style="width:19%">
        <col style="width:38%">
        <col style="width:14%">
        <col style="width:11%">
        <col style="width:18%">
      </colgroup>
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(order, i) in orderDetailInfo" :key="i">
          <td class="img"><img :src="stored_thumbnail[i]" /></td>
          <td>
            <strong class="itemName" v-if="productNameList[i]">{{productNameList[i]}}</strong>
          </td>
          <td>{{order.order_price}}</td>
          <td>{{order.order_count}}</td>
          <td>{{order.order_count * order.order_price}}</td>
        </tr>
      </tbody>
    </table>

    <p class="group"> <span class="modifyText">( *  )</span></p>
    <table class="table t2">
      <colgroup>
        <col width="20%">
        <col width="30%">
      </colgroup>
      <tbody>
        <tr>
          <th scope="col"></th>
          <td><input type="text" :value="orderGroupInfo.member_id" style="outline:none;" readonly></td>
          <th scope="col"></th>
          <td><input type="text" v-if="orderGroupInfo.created_at" :value="orderGroupInfo.created_at.split('T')[0]" style="outline:none;" readonly></td>
        </tr>
        <tr v-if="orderGroupInfo.pay_method === 'BANK_TRANSFER'">
          <th scope="col"></th>
          <td><input type="text" value="" style="outline:none;" readonly></td>
          <th scope="col"></th>
          <td><input type="text" :value="this.bank" style="outline:none;" readonly></td>
        </tr>
        <tr v-if="orderGroupInfo.pay_method === 'CARD'">
          <th scope="col"></th>
          <td><input type="text" value="" style="outline:none; text-align:center;" readonly></td>
          <!-- <th scope="col"></th>
          <td><input type="text" :value="this.bank" style="outline:none;" readonly></td> -->
        </tr>
        <tr>
          <th scope="col"></th>
          <td><input type="text" :value="orderGroupInfo.depositor" style="outline:none;" readonly></td>
          <th scope="col">*</th>
          <td><input type="text" v-model="phone"></td>
        </tr>
        <tr>
          <th scope="col"></th>
          <td><input type="text" :value="orderGroupInfo.total_price + ''" style="outline:none;" readonly></td>
          <th scope="col">*</th>
          <td>
            <select name="order" class="op" @change="optionChange($event)">
              <option value="BEFORE_BANK_TRANSFER" :selected="orderGroupInfo.order_product_state === 'order_product_state'"></option>
              <option value="READY" :selected="orderGroupInfo.order_product_state === 'READY'"></option>
              <option value="SHIPPING" :selected="orderGroupInfo.order_product_state === 'SHIPPING'"></option>
              <option value="COMPLETE" :selected="orderGroupInfo.order_product_state === 'COMPLETE'"></option>
              <option value="CARD" :selected="orderGroupInfo.order_product_state === 'CARD'"></option>
            </select>
          </td>
        </tr>
      </tbody>
    </table>

    <p class="group"> <span class="modifyText">(  )</span></p>
    <table class="table t3">
      <colgroup>
        <col width="10%">
        <col width="20%">
        <col width="15%">
        <col width="20%">
        <col width="15%">
        <col width="20%">
      </colgroup>
      <tbody>
        <tr>
          <th scope="col"></th>
          <td><input type="text" v-model="recipient"></td>
          <th scope="col"></th>
          <td><input type="text" v-model="postCode"></td>
          <th scope="col"></th>
          <td class="changeBtn"><a @click="showApi">click</a></td>
        </tr>
        <tr>
          <th scope="col"></th>
          <td colspan="3"><input type="text" v-model="address"></td>
          <th scope="col"></th>
          <td><input type="text" v-model="detailAddress"></td>
        </tr>
        <tr>
          <th scope="col"></th>
          <td colspan="5"><input type="text" v-model="request"></td>
        </tr>
      </tbody>
    </table>

    <div class="button">
      <v-btn color="#000" style="color:white;" x-large @click="list"></v-btn>
      <v-btn color="#000" style="color:white;" x-large @click="cancle"></v-btn>
      <v-btn color="#000" style="color:white;" x-large @click="save"></v-btn>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      orderGroupInfo: {},
      orderDetailInfo: [],
      bank: '',
      productNameList: [],
      detailId: [],
      postCode: 0,
      address: '',
      detailAddress: '',
      phone: '',
      recipient: '',
      request: '',
      option: '',
      stored_thumbnail: [],
      orderCount: [],
      itemId: []
    }
  },
  methods: {
    optionChange (event) {
      this.option = event.target.value
    },
    cancle () {
      this.$swal.fire({
        icon: 'warning',
        title: '    .',
        text: ' ?',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        confirmButtonColor: '#FE9A2E',
        cancelButtonColor: '#BDBDBD',
        cancelButtonText: 'No'
      }).then(async (result) => {
        if (result.isConfirmed) {
          for (let i = 0; i < this.detailId.length; i++) {
            await axios
              .delete(`http://localhost:8000/jewelry/orderDetail/${this.detailId[i]}`, {
              })
              .then(function (response) {
                console.log(response)
              })
              .catch(function (error) {
                console.log(error)
              })
            axios
              .put('http://localhost:8000/jewelry/item/update/stockplus', {
                id: this.itemId[i],
                stock: this.orderCount[i]
              })
              .then((res) => {
                console.log(res)
              })
              .catch((error) => {
                console.log(error)
              })
          }
          await axios
            .delete(`http://localhost:8000/jewelry/orderGroup/${this.orderGroupInfo.id}`, {
            })
            .then(function (response) {
              console.log(response)
            })
            .catch(function (error) {
              console.log(error)
            })
          await this.$router.push('/adorder')
        }
      })
    },
    list () {
      this.$swal.fire({
        icon: 'warning',
        title: '  .',
        text: ' ?',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        confirmButtonColor: '#FE9A2E',
        cancelButtonColor: '#BDBDBD',
        cancelButtonText: 'No'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$router.push('/adorder')
        }
      })
    },
    save () {
      axios
        .put('http://localhost:8000/jewelry/orderGroup/update', {
          delivery_message: this.request,
          resipient: this.recipient,
          order_product_state: this.option,
          post_code: this.postCode,
          address: this.address,
          detail_address: this.detailAddress,
          phone: this.phone,
          id: this.orderGroupInfo.id
        })
        .then((res) => {
          console.log(res)
          this.$swal.fire({
            icon: 'success',
            title: ' .',
            text: ' .',
            confirmButtonColor: '#CEF6CE'
          }).then(() => {
            this.$router.push('/adorder')
          })
        })
        .catch((error) => {
          console.log(error)
        })
    },
    showApi () {
      new window.daum.Postcode({
        oncomplete: (data) => {
          //        .
          //       .
          //      ('') ,    .
          let fullRoadAddr = data.roadAddress
          //   
          let extraRoadAddr = ''
          //    
          //    . ( )
          //     "//" .
          if (data.bname !== '' && /[||]$/g.test(data.bname)) {
            extraRoadAddr += data.bname
          } //  ,   .
          if (data.buildingName !== '' && data.apartment === 'Y') {
            extraRoadAddr +=
              extraRoadAddr !== ''
                ? ', ' + data.buildingName
                : data.buildingName
          }
          // ,     ,     .
          if (extraRoadAddr !== '') {
            extraRoadAddr = ' (' + extraRoadAddr + ')'
          }
          if (fullRoadAddr !== '') {
            fullRoadAddr += extraRoadAddr
          }
          this.postCode = data.zonecode
          this.address = fullRoadAddr
          this.detailAddress = ''
        }
      }).open()
    },
    orderDetail () {
      this.stored_thumbnail = []
      return axios.get(`http://localhost:8000/jewelry/orderGroup/${this.$store.state.order.orderId}/orderDetailInfo`)
        .then(async res => {
          this.orderGroupInfo = res.data.data.order_group_response
          this.orderDetailInfo = res.data.data.order_group_response.order_detail_response_list

          this.postCode = this.orderGroupInfo.post_code
          this.address = this.orderGroupInfo.address
          this.detailAddress = this.orderGroupInfo.detail_address
          this.phone = this.orderGroupInfo.phone
          this.recipient = this.orderGroupInfo.resipient
          this.request = this.orderGroupInfo.delivery_message
          this.option = this.orderGroupInfo.order_product_state

          for (let i = 0; i < this.orderDetailInfo.length; i++) {
            this.detailId.push(this.orderDetailInfo[i].id)
            this.itemId.push(this.orderDetailInfo[i].item_id)
            this.orderCount.push(this.orderDetailInfo[i].order_count)

            await axios.get('http://localhost:8000/jewelry/item/' + this.orderDetailInfo[i].item_id + '/itemInfo')
              .then(res => {
                let tmp = res.data.data.item_response.image_file_response_list.findIndex(
                  (i) => i.delegate_thumbnail === 'YES'
                )
                if (res.data.data.item_response.image_file_response_list.length !== 0) {
                  if (tmp === -1) this.stored_thumbnail.push(res.data.data.item_response.image_file_response_list[0].stored_file_name)
                  else this.stored_thumbnail.push(res.data.data.item_response.image_file_response_list[tmp].stored_file_name)
                }
              })
          }

          if (this.orderGroupInfo.pay_account === 'IBK') this.bank = '000-00000000-00'
          else this.bank = '111-11111111-11'

          this.productName()
        })
        .catch(err => {
          console.log(err)
        })
    },
    async productName () {
      for (let i = 0; i < this.orderDetailInfo.length; i++) {
        const id = this.orderDetailInfo[i].item_id
        await axios.get(`http://localhost:8000/jewelry/item/${id}`)
          .then(res => {
            this.productNameList.push(res.data.data.name)
          })
          .catch(err => {
            console.log(err)
          })
      }
    }
  },
  created () {
    this.orderDetail()
  }
}
</script>

<style scoped>
.outer {
  margin: 4rem auto;
  width: 53rem;
  text-align: center;
}
p.top {
  font-weight: 700;
  font-size: 1.5rem;
  margin-bottom: 3rem;
}

.modifyText {
  font-size: 0.7rem;
}

.group {
  float: left;
  margin-bottom: 2rem;
  font-weight: 700;
}
.boxs {
  width: 53rem;
  margin-bottom: 4rem;
}
.box {
  display: flex;
  margin: 2rem auto;
  border-bottom: 0.15rem solid black;;
}
.img, .content {
  height: 100px;
}
.table > :not(:first-child) {
  border: 1px solid gray;
}
.img {
  width: 20%;
}
img {
  width: 90px;
  height: 90px;
  background-size: cover;
}
.count,
.price {
  width: 16%;
  padding-top: 30px;
}
.content {
  width: 16%;
  padding-top: 30px;
}
.op {
  border: 1px solid gray;
  width: fit-content;
  font-size: 0.8rem;
  border-radius: 2px;
  padding: 0.2rem 0.8rem;
  background-color: white;
  text-align: center;
  color:gray;
}

.table {
  width: 850px;
  border-top: 0.2rem solid black;
  border-bottom: 0.2rem solid black;
  margin-bottom: 5rem;
}
input {
  text-align: center;
  width: 100% !important;
  font-size: 0.8rem;
}

.table tr {
  border-bottom: 1.5px solid gray;
  border-right: 1px solid black;
}
.table th {
  padding: 0.6rem 0;
  font-size: 0.85rem;
  background-color: #e7e7e7;
  border-right: 1px solid black;
  border-left: 1px solid black;
  vertical-align: middle;
}

.changeBtn a {
  text-decoration: none;
  color: black;
  border: 1px solid black;
  border-radius: 10px;
  padding: 0.2rem 1.5rem;
  font-size: 0.8rem;
  font-weight: 700;
  background-color: #555;
  box-shadow: 1px 0.5px 0 rgb(0,0,0,0.5);
  cursor: pointer;
}
.changeBtn a:active {
  box-shadow: 1px 0px 0 rgb(0,0,0,0.5);
  position: relative;
  top: 0.5px;
}

.info {
  margin: 1rem 0 3rem;
  border: 2px solid black;
  border-left: none;
  border-right: none;
  width: 850px;
}
.info thead {
  border-bottom: 1px solid #e7e7e7;
  background-color: #e7e7e7;
}
.info thead th {
  padding: 1rem 0;
  vertical-align: middle;
}
.info tbody td {
  padding: 1rem 0;
  vertical-align: middle;
}
.info tbody tr {
  border-bottom: 0.5px solid black;
}

.button button {
  border: 1px solid black;
  margin: 0 0.6rem;
  padding: 0.5rem 2rem;
  font-weight: 700;
}
.button {
  margin-bottom: 2rem;
}

</style>



