<template>
  <div class="outer">
    <p></p>
    <!-- content-->
    <div id="content">
      <!-- NAME -->
      <form name="form" @submit.prevent="handleRegister">
        <div v-if="!successful">
          <h3 class="join_title"><label for="name"></label></h3>
          <span class="box int_name">
            <input
              placeholder=" 3~20  "
              v-model="user.name"
              v-validate="'required'"
              type="text"
              name="name"
              id="name"
              class="int"
              minlength="3"
              maxlength="20"
              autocomplete="no"
            />
          </span>
          <div v-if="submitted && errors.has('name')" class="errorMessage">
             3~20  
          </div>
          <span>{{ errors[0] }}</span>
        </div>
        <!-- ID -->
        <div v-if="!successful">
          <h3 class="join_title">
            <label for="id"></label>
          </h3>
          <span class="box int_id">
            <input
              placeholder=" 3~20  "
              v-model="user.account"
              v-validate="'required'"
              type="text"
              name="account"
              id="id"
              class="int"
              minlength="3"
              maxlength="20"
              autocomplete="off"
            />
          </span>
          <div v-if="submitted && errors.has('account')" class="errorMessage">
             3~20  
          </div>
        </div>

        <!-- PW1 -->
        <div v-if="!successful">
          <h3 class="join_title"><label for="pswd1"></label></h3>
          <span class="box int_pass">
            <input
              autocomplete="off"
              ref="password"
              placeholder=" 6~20  "
              v-model="user.password"
              v-validate="'required|'"
              minlength="6"
              maxlength="20"
              name="password"
              type="password"
              id="pswd1"
              class="int"
            />
          </span>
          <div v-if="submitted && errors.has('password')" class="errorMessage">
             6~20  .
          </div>
        </div>

        <!-- PW2 -->
        <div v-if="!successful">
          <h3 class="join_title"><label for="pswd2"> </label></h3>
          <span class="box int_pass_check">
            <input
              autocomplete="off"
              type="password"
              id="pswd2"
              v-validate="'required|confirmed:password'"
              name="confirm_password"
              class="int"
              minlength="6"
              maxlength="20"
              placeholder="  ."
            />
          </span>
          <div v-if="errors.has('confirm_password')" class="errorMessage">
               .
          </div>
        </div>

        <!-- EMAIL -->
        <div v-if="!successful">
          <h3 class="join_title">
            <label for="email"></label>
          </h3>
          <span class="box int_email">
            <input
              autocomplete="off"
              v-model="user.email"
              v-validate="'required'"
              maxlength="30"
              type="email"
              id="email"
              name="email"
              class="int"
              placeholder="   "
            />
          </span>
          <div
            v-if="submitted && errors.has('email')"
            class="errorMessage"
            role="alert"
          >
               (ex)test@naver.com
          </div>
        </div>

        <!-- MOBILE -->
        <div v-if="!successful">
          <h3 class="join_title"><label for="phoneNo"></label></h3>
          <div class="phone">
            <div class="w_33">
              <select
                class="form-select ht51"
                id="mobile"
                v-validate="'required'"
                v-model="phone1"
                name="phone1"
              >
                <option class="option_disable" disabled value="">
                   
                </option>
                <option value="010">010</option>
                <option value="011">011</option>
                <option value="016">016</option>
                <option value="017">017</option>
                <option value="019">019</option>
              </select>
            </div>
            <div class="box int_mobile">
              <input
                autocomplete="no"
                v-model="phone2"
                v-validate="'numeric'"
                maxlength="4"
                name="phone"
                type="tel"
                class="int phone"
              />
            </div>
            <div class="box int_mobile">
              <input
                autocomplete="no"
                v-model="phone3"
                v-validate="'numeric'"
                name="phone"
                maxlength="4"
                type="tel"
                class="int phone"
              />
            </div>
          </div>
          <div class="error_div">
            <div
              v-if="submitted && errors.has('phone1')"
              class="errorMessage clear"
              role="alert"
            >
                
            </div>
            <div
              v-if="submitted && errors.has('phone')"
              class="errorMessage clear"
              role="alert"
            >
                
            </div>
          </div>
        </div>
        <!-- ADDRESS -->
        <div v-if="!successful">
          <h3 class="join_title"><label for="phoneNo"></label></h3>
          <div class="addr">
            <div class="box addr1">
              <input
                :disabled="'disabled'"
                name="postCode"
                type="text"
                id="addr"
                class="int"
                maxlength="8"
                placeholder=" "
                v-model="user.postCode"
              />
            </div>
            <div class="form-group btn_area">
              <button @click="showApi" type="button" id="btnAddr">
                <span> </span>
              </button>
            </div>
            <span class="box addr2">
              <input
                name="address"
                :disabled="'disabled'"
                type="tel"
                id="addr"
                class="int"
                maxlength="16"
                placeholder=" "
                v-model="user.address"
              />
            </span>
            <span class="box addr3">
              <input
                autocomplete="off"
                name="detailAddress"
                type="tel"
                id="addr"
                class="int"
                maxlength="16"
                placeholder="  "
                v-validate="'required'"
                v-model="user.detailAddress"
              />
            </span>
            <div
              v-if="submitted && errors.has('detailAddress')"
              class="errorMessage clear"
              role="alert"
            >
               
            </div>
          </div>
        </div>
        <div class="form-group btn_area">
          <button id="btnJoin" value="">
            <span></span>
          </button>
        </div>

        <!-- JOIN BTN-->
      </form>
    </div>
  </div>
</template>

<script>
import User from '../../models/user'

export default {
  name: 'Register',
  data () {
    return {
      user: new User('', '', '', '', '', '', '', ''),
      submitted: false,
      successful: false,
      message: '',
      phone1: '',
      phone2: '',
      phone3: ''
    }
  },
  computed: {
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  mounted () {
    if (this.loggedIn) {
      this.$router.push('/')
    }
  },
  methods: {
    handleRegister () {
      this.message = ''
      this.submitted = true
      this.$validator.validate().then((isValid) => {
        if (isValid) {
          this.user.phone = this.phone1 + '-' + this.phone2 + '-' + this.phone3
          this.$store.dispatch('auth/register', this.user).then(
            (data) => {
              this.successful = true
              this.$swal.fire({
                position: 'center',
                icon: 'success',
                title: ' .',
                timer: 3000,
                showConfirmButton: true,
                confirmButtonColor: '#a5dc86',
                footer: 'GGULUCK   !'
              })
              this.$router.push('/')
            },
            (error) => {
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString()
              if (
                this.message ===
                'could not execute statement; SQL [n/a]; constraint [CLINUT.UKD7QQV98P5470L9VJP4J6YTOMD]; nested exception is org.hibernate.exception.ConstraintViolationException: could not execute statement'
              ) {
                this.$swal.fire({
                  position: 'center',
                  icon: 'success',
                  title: ' .',
                  showConfirmButton: true,
                  confirmButtonColor: '#a5dc86',
                  timer: 3000,
                  footer: 'GGULUCK   !'
                })
                this.$router.push('/')
              } else {
                this.$swal.fire({
                  icon: 'error',
                  title: ' ',
                  showConfirmButton: true,
                  confirmButtonColor: '#F27474',
                  footer: this.message
                })
              }
              this.successful = false
            }
          )
        } else {
          this.$swal.fire({
            icon: 'warning',
            title: '  <br>   .',
            showConfirmButton: true,
            confirmButtonColor: '#F8BB86',
            footer: '     '
          })
          this.successful = false
        }
      })
    },
    showApi () {
      new window.daum.Postcode({
        oncomplete: (data) => {
          //        .
          //       .
          //      ('') ,    .
          let fullRoadAddr = data.roadAddress
          //   
          let extraRoadAddr = ''
          //    
          //    . ( )
          //     "//" .
          if (data.bname !== '' && /[||]$/g.test(data.bname)) {
            extraRoadAddr += data.bname
          } //  ,   .
          if (data.buildingName !== '' && data.apartment === 'Y') {
            extraRoadAddr +=
              extraRoadAddr !== ''
                ? ', ' + data.buildingName
                : data.buildingName
          }
          // ,     ,     .
          if (extraRoadAddr !== '') {
            extraRoadAddr = ' (' + extraRoadAddr + ')'
          }
          if (fullRoadAddr !== '') {
            fullRoadAddr += extraRoadAddr
          }
          this.user.postCode = data.zonecode
          this.user.address = fullRoadAddr
        }
      }).open()
    }
  }
}
</script>

<style scoped>
.outer {
  margin: 5rem 0 0 28%
}
p {
  font-weight: 700;
  font-size: 1.5rem
}
#content {
  width: 45vw;
  margin-top: 3rem
}
h3 {
  margin: 19px 0 8px;
  font-size: 14px;
  font-weight: 700
}
.box {
  display: block;
  width: 100%;
  height: 51px;
  border: solid 1px #dadada;
  padding: 10px 14px 10px 14px;
  box-sizing: border-box;
  background: #fff;
  position: relative
}
.int_mobile {
  width: 33.3% !important
}
.phone {
  display: flex
}
.int {
  display: block;
  position: relative;
  width: 100%;
  height: 29px;
  border: none;
  background: #fff;
  font-size: 15px
}
input {
  font-family: Dotum, "", Helvetica, sans-serif;
  outline: 0
}
select {
  width: 100% !important
}
.box.int_id {
  padding-right: 110px
}
.box.int_pass {
  padding-right: 40px
}
.box.int_pass_check {
  padding-right: 40px
}
.btn_area {
  margin: 30px 0 91px
}
#btnJoin {
  width: 100%;
  padding: 21px 0 17px;
  border: 0;
  cursor: pointer;
  color: #fff;
  background-color: #b4b9be;
  font-size: 20px;
  font-weight: 700;
  font-family: Dotum, "", Helvetica, sans-serif
}
#btnJoin:hover {
  background-color: #000
}
.errorMessage {
  font-size: 12px;
  line-height: 42px;
  font-weight: 700;
  color: #ff003e;
  vertical-align: middle;
  float: right
}
input::placeholder {
  color: #c9c4c4;
  font-size: 15px;
  font-weight: 700
}
.ht51 {
  height: 51px;
  border-radius: 0
}
.w_33 {
  width: 33.3%
}
.option_disable {
  color: #c9c4c4;
  font-weight: 700
}
.form-select {
  color: #000;
  font-weight: 700
}
.int_email,
.int_id,
.int_name,
.phone {
  color: #000;
  font-weight: 700
}
.clear {
  clear: both
}
#btnAddr {
  width: 50%;
  padding: 21px 0 17px;
  border: 0;
  cursor: pointer;
  color: #fff;
  background-color: #b4b9be;
  font-weight: 700;
  font-family: Dotum, "", Helvetica, sans-serif;
  font-size: 14px;
  height: 51px;
  line-height: 1px
}
#btnAddr:hover {
  background-color: #000
}
.addr {
  width: 100%
}
.addr1 {
  color: #000;
  font-weight: 700;
  width: 50%;
  float: left;
  height: 51px
}
.addr2 {
  color: #000;
  font-weight: 700;
  margin-top: -85px;
  margin-bottom: 7px
}
.addr3 {
  color: #000;
  font-weight: 700
}
.form-select {
  outline: 0 !important;
  box-shadow: none;
  border-color: #b4b9be
}
</style>



