<template>
  <div class="outer">
    <p class="top"></p>
    <div class="boxs">
      <div @click="admember">
        <p>MEMBER</p>
        <p></p>
        <p class="material-icons-outlined">manage_accounts</p>
        <p>   <br>   </p>
      </div>
      <div @click="adproduct">
        <p>PRODUCT</p>
        <p></p>
        <p class="material-icons-outlined">inventory_2</p>
        <p>  <br>   </p>
      </div>
      <div @click="adorder">
        <p>ORDER</p>
        <p></p>
        <p class="material-icons-outlined">list_alt</p>
        <p>   <br>   </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    admember () {
      this.$router.push('/admember')
    },
    adproduct () {
      this.$router.push('/adproduct')
    },
    adorder () {
      this.$router.push('/adorder')
    },
    adsales () {
      this.$router.push('/adsales')
    }
  }
}
</script>

<style scoped>
.outer {
  margin: 6rem auto;
  width: 62rem;
  text-align: center;
}
p.top {
  font-weight: 700;
  font-size: 1.5rem;
}
.boxs {
  display: flex;
  width: 62rem;
  margin: 6rem auto;
  margin-bottom: 12rem;
}
.boxs div {
  width: 33%;
  padding: 0 2.2rem;
  cursor: pointer;
}
.boxs div p {
  margin-bottom: 0;
}
.boxs div p:nth-child(1) {
  font-size: 1.3rem;
}
.boxs div p:nth-child(2) {
  font-size: 1rem;
  color: rgb(255, 73, 73);
}
.boxs div p:nth-child(3) {
  font-size: 3rem;
}
.boxs div p:nth-child(4) {
  font-size: 0.8rem;
}

</style>



